                    <p>In the current scenario the scarcity of time sometimes outrails people leading to inefficiency in plants proper management schedule.  The solution of this problem is <b>MyPlantsTalk</b>, a real time plant monitoring system. This special pot will trace the real time conditions of your plant and gives you the status of your plant through an app. This system notifies you with all the subtle requirements your plant is in need of. It will also monitoring the watering, sunlight and other conditions of plant. This will also tell you what are the suitable conditions for your plant and in which conditions your plant is growing. So further one can adjust accordingly as per the necessities for better results. In case of any unsuited circumstances the system will alert you via application. This will also tell you about all the benefits.</p>

